.input-group{
  display: flex;
  flex-direction: column;
  margin-bottom: $m-size;

  @media (min-width: $desktop-breakingpoint){
    flex-direction: row;
    margin-bottom: $l-size;
  }
}

.input-group__item{
  margin-bottom: $s-size;
  @media (min-width: $desktop-breakingpoint){
    margin: 0 $s-size 0 0;
  }
}
